@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&family=Rubik&display=swap);
header {
    text-align: center;
    margin: 30px 20px;
}

header img{
    width: 130px;
}
.banner-main {
    height: 500px;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.banner-main .content {
    margin: auto;
}

.banner-main .content h1{      
    font-weight: 100;
    width: 80%;
    margin: auto;
    line-height: 67px;
}

.banner-main .content p {
    margin-top: 15px;    
    color: #A5A5A5;
}

.banner-img {
    display: block;
    margin: auto;
    max-width: 100%;
    height: auto;
}

.column {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 190px;
}

.column .text-col h2{    
    font-weight: 100;    
}
.column .text-col p{    
    width: 60%;    
    color: #A5A5A5;
    margin-top: 15px;
}

.img-col img{
    max-width: 100%;
    height: auto;
}

.column-reverse {
    flex-direction: row-reverse;
}

.column-reverse .text-col{
    text-align: end;
}

.column-reverse .text-col p {
    margin-left: auto;
}

.btn {
    padding: 10px 20px;
    border: 0;
    margin-top: 17px;
}

.btn-primary {
    border-radius: 50px;
    color: #0088FF;
    font-weight: bold;
    background-color: #fff;
}

/* https://codepen.io/dfmcphee/pen/pgrGmW?editors=1100 */

@media (max-width:800px) {
    .column {
       flex-direction: column-reverse;
    }

    .img-col {
        width: 100%;        
    }

    .text-col {
        margin-top: 30px;
    }

    .column .text-col p {
        width: auto;
    }

    .column-reverse .text-col {
        text-align: inherit;
    }    
}

@media (max-width: 620px) {
    .banner-main .content h1 {
        width: auto;
        font-size: 59px;
    }
}
.cta {
    margin-top: 100px;
    background: linear-gradient(135deg, rgba(0,136,255,1) 0%, rgba(100,180,251,1) 100%);
    height: 300px;
    display: flex;
    flex-direction: column;
    text-align: center;
    color: #fff;
}

.cta .content {
    margin: auto;
}
footer {
    padding: 50px 0;
    display: flex;
    align-items: center;
}

footer img{
    width: 115px;
}

footer span{
    font-family: 'Rubik', sans-serif;
    font-size: 9px;
    font-weight: bold;
    margin-left: 5px;
}
* {
    margin: 0;
    padding: 0;
}

.container {
    max-width: 1360px;
    width: 80%;
    margin: auto;
}

body {
    outline: none;    
    font-size: 18px;
}

h1, h2 {
    font-family: 'DM Serif Display', serif;
}

h1 {
    font-size: 68px;  
    font-weight: 100;
}

h2 {
    font-size: 45px;
    font-weight: 100;
}

button {
    cursor: pointer;
    outline: none;
}

p, button {
    font-family: 'Rubik', sans-serif;
}


